import HeaderBack from "components/HeaderBack";
import Layout from "components/Layout";
import DetailFooter from "./component/DetailFooter";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import * as DOMPurify from "dompurify";

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

const MOCK_DATA = {
  productId: 1,
  price: 100000,
  registDate: "2024-02-17",
  productImages: [],
  title: " .",
  content: `    <br />
         
  <br />        <br />
          <br />
        <br />
  <br />
      <br />     
    <br />      
  <br />
  <br />        `,
};

const MOCK_USERINFO = {
  userId: "_",
  userMannerScore: "36.0",
};

const ProductDetail = () => {
  const { id } = useParams();
  const [productInfo, setProductInfo] = useState(MOCK_DATA);

  useEffect(() => {
    getData();
  });

  //   redux  
  const getUserInfo = () => {
    return MOCK_USERINFO;
  };

  const getData = () => {
    setProductInfo(MOCK_DATA);
  };

  const { userId, userMannerScore } = getUserInfo();

  return (
    <Layout
      id={"detail"}
      CustomHeader={HeaderBack}
      CustomFooter={DetailFooter}
      FooterOptions={{ data: productInfo }}
    >
      {/* <!--   :    --> */}
      <Slider
        dots
        speed={500}
        slidesToShow={1}
        slidesToScroll={1}
        arrows={false}
      >
        <div className="banner">
          <img src="../assets/img/sample500.png" alt="" />
        </div>
        <div className="banner">
          <img src="../assets/img/sample500.png" alt="" />
        </div>
        <div className="banner">
          <img src="../assets/img/sample500.png" alt="" />
        </div>
        <div className="banner">
          <img src="../assets/img/sample500.png" alt="" />
        </div>
      </Slider>

      {/* <!--    --> */}
      <div className="seller-info">
        {/* <!--  :  --> */}
        <div className="profile">
          <img src="../assets/img/sample500.png" />
        </div>
        {/* <!--  :  --> */}
        <div className="seller-id">{userId}</div>
        {/* <!--  :  --> */}
        <div className="seller-score">
          <div> </div>
          <div>{userMannerScore}</div>
        </div>
      </div>

      {/* <!--    --> */}
      <div className="product-info">
        {/* <!--   :  --> */}
        <div className="title">{productInfo.title}</div>
        {/* <!--   :  --> */}
        <div className="product-date">{productInfo.registDate}</div>
        {/* <!--   :   --> */}
        <div
          className="description"
          dangerouslySetInnerHTML={{
            __html: DOMPurify.sanitize(productInfo.content),
          }}
        />
      </div>

      {/* <!--    :   --> */}
      <div>
        <div className="mini-title">  </div>
        <div className="container">
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">  z5 ..!</div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">     </div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">
                     
            </div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">
                         
              
            </div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">  .</div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">Mac Air  </div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title"> </div>
            <div className="date">2024-02-17</div>
          </div>
          <div className="item">
            <img src="../assets/img/sample500.png" alt="" />
            <div className="title">  react  </div>
            <div className="date">2024-02-17</div>
          </div>
        </div>
      </div>
    </Layout>
  );
};

export default ProductDetail;
