import * as URL from "../URL";

import NotFound from "NotFound";
import HomePage from "pages/Home/HomePage";
import LoginPage from "pages/Login/LoginPage";
import ProductDetail from "pages/ProductDetail/ProductDetailPage";
import Search from "pages/Search/SearchPage";

/**
 * route  
 * @param path : [string] URL  . URL.js 
 * @param exact : [boolean] true  URL  routing . true
 * @param name : [string]    
 * @param component : [Component]   
 * @param isPrivate : [boolean] true      
 * @param headerType : [string]   header  
 */

const routes = [
  {
    path: URL.URL_LOGIN,
    exact: true,
    name: "",
    component: LoginPage,
    isPrivate: false,
  },
  {
    path: URL.URL_HOME,
    exact: true,
    name: "",
    component: HomePage,
    isPrivate: true,
  },
  {
    path: `${URL.URL_PRODUCT_DETAIL}/:id`,
    exact: false,
    name: "",
    component: ProductDetail,
    isPrivate: true,
  },
  {
    path: URL.URL_SEARCH,
    exact: false,
    name: "",
    component: Search,
    isPrivate: true,
  },
  {
    path: URL.URL_NOT_FOUND,
    exact: true,
    name: "  ",
    component: NotFound,
  },
];

export default routes;
